export default function (props, ref, key) {
  return (
    <skoash.Screen
      {...props}
      ref={ref}
      key={key}
      id="ground"
    >
      <skoash.Audio ref="vo" type="voiceOver" src="media/S_6/VO_6.1.mp3" />
      <skoash.Audio ref="button" complete type="sfx" src="media/S_4/VO_4.2.mp3" />
      <skoash.Image ref="image-1" class="animated" src="media/S_6/img_6.1.png" />
      <skoash.Image ref="image-2" src="media/S_6/img_6.2.png" />
    </skoash.Screen>
  );
}



