export default function (props, ref, key) {
  return (
    <skoash.Screen
      {...props}
      ref={ref}
      key={key}
      id="school"
    >
      <skoash.Audio ref="vo" type="voiceOver" src="media/S_5/VO_5.1.mp3" />
      <skoash.Audio ref="button" complete type="sfx" src="media/S_4/VO_4.2.mp3" />
      <skoash.Image ref="image-1" src="media/S_5/img_5.1.png" />
      <skoash.Image ref="image-2" class="animated" src="media/S_5/img_5.2.png" />
    </skoash.Screen>
  );
}



