export default function (props, ref, key) {
  return (
    <skoash.Screen
      {...props}
      ref={ref}
      key={key}
      id="sing-about-it"
    >
      <skoash.Audio ref="vo" type="voiceOver" src="media/S_7/VO_7.1.mp3" />
      <skoash.Image ref="image" className="animated" src="media/S_7/img_7.1.png" />
    </skoash.Screen>
  );
}



